<template>
<!-- <p>我是首页</p> -->


<div class="home">
 
<!-- 顶部栏 -->
<mt-header fixed title="购物街"></mt-header>

<listbar   :toptitle='["流行","新款","精选"]'  
@barclicked="getdata" ref="newlistbar" :nowindex="currentindex"
v-show="newlbarisshow" class="newlistbar"/>

<scroll :probetype="3" ref="scroll" @scrolling="getposition"
@pullupload="loadmore" >

<!-- 轮播图 -->
<lunbo :imglist= topres.lunboimgs @imghasload="lunboimgload" />

 <!-- 推荐栏 -->
<recdbar  :recommend="topres.recd" @imgLoad="recdimgLoad" />

<!-- 流行栏 -->
<popbar :pop="topres.pop" @popimgLoad="popimgload" 

/>

<!-- 货物列表顶部栏 -->
<listbar   :toptitle='["流行","新款","精选"]'  
 @barclicked="getdata" ref="listbar" :nowindex="currentindex"/>

<!-- 货物列表区 -->
<list   :goods="nowgoods" 
@imgload="listimghasload"   />

</scroll>
<totop  @click.native="backtop" v-show="totopisshow"/>
</div>


</template>

<script>
import Lunbo from "../../common/lunbo.vue"
import Recdbar from './recdbar.vue'
import Popbar from './popbar.vue'
import Listbar from "./listbar.vue"
import List from "./list.vue"
import bscroll from "better-scroll"
import Scroll from '../../common/scroll.vue'
import Totop from '../../common/totop.vue'
import {debounce} from "../../../assets/js/tool.js"
import {topdata,goodsdata} from "./data.js"
import net from "../../../axios/homenet.js"



export default {
   data:function(){
     return{
      topres:"",
       goodsres:"",
       nowgoods:[],
       currentindex:0,
       totopisshow:false,
       listbartopvalue:"",
       newlbarisshow:false,
       oldy:"",
       myrefresh:"",
     }
   },
   components:{
     lunbo:Lunbo,
     recdbar:Recdbar,
     popbar:Popbar,
     listbar:Listbar,
     list:List,
     scroll:Scroll,
     totop:Totop,
      
   },
   methods:{
      net:net,
      getdata:function(index){
          this.currentindex=index;
          if(index==0)
          this.nowgoods=this.goodsres.pop;
          else if(index==1)
          this.nowgoods=this.goodsres.new;
          else
           this.nowgoods=this.goodsres.delicate;

          //  console.log(this.nowgoods)
      },
      backtop:function(){
         this.$refs.scroll.backTo(0,0);
     
      },
      getposition:function(position){
         //console.log(-position.y)
         if(-position.y>360)
          this.totopisshow=true
          else
          this.totopisshow=false;

         if(-position.y>this.listbartopvalue)
         this.newlbarisshow=true
         else
         this.newlbarisshow=false
      },
      lunboimgload:function(){
         this.$refs.scroll.refresh();
         this.listbartopvalue=this.$refs.listbar.$el.offsetTop;
      },
      recdimgLoad:function(){
         //当scroll出现滚不到底部的问题就调用这个函数
          this.$refs.scroll.refresh();
      },
      popimgload:function(){
        //获取列表底部栏的top值
        
        //在图片加载完再计算列表底部栏的top值才是正确的
        //因为该图片较大，而且影响布局，因此需要等待它加载完
        //通过组件对象的$el属性拿到Dom对象
       //console.log( this.$refs.listbar.$el.offsetTop);
       this.listbartopvalue=this.$refs.listbar.$el.offsetTop;
       this.$refs.scroll.refresh();
      },
      loadmore:function(){
     
   if(this.nowgoods[0].id==this.goodsres.pop[0].id)
  {
  // this.nowgoods.push.apply(this.nowgoods,this.goodsres.pop)
  //在这里面使用push.apply方法会使得另一个数组也拼接，但
  //在原生里是不会的，具体原因不明白，因此在这里不适用
  
  this.nowgoods=this.nowgoods.concat(this.goodsres.pop);
  // console.log("pop")
 }
  else if(this.nowgoods[0].id==this.goodsres.new[0].id)
  {
  this.nowgoods=this.nowgoods.concat(this.goodsres.new)
  //console.log("new")
   }
  else
  {
 this.nowgoods=this.nowgoods.concat(this.goodsres.delicate)
  //console.log("delicate")
  } 
     this.$refs.scroll.pullupend();
     //this.$refs.scroll.refresh();
     this.$refs.scroll.refresh();
     },
   listimghasload:function(){
  //  this.betterrefresh();
  //这里我们不使用防抖函数，因为加载资源较少，使用它
  //可能导致出错
   this.$refs.scroll.refresh();
      },
   },
  created:function(){
     //网络请求代码,当组件被创建时就开始请求数据
     this.net();  
   
    //const tophalfdata=topdata;
    //this.topres=tophalfdata;
    
    //const goods=goodsdata;
    //this.goodsres=goods;
     //this.nowgoods=goods.pop;
    },
  mounted:function(){
  //当组件创建完毕时调用
   this.betterrefresh=debounce(this.$refs.scroll.refresh)
   //debounce,防抖函数，提高性能，通过延时减少函数执行次数
  //对refresh方法进行优化，防抖处理
},
activated:function(){
    //当进入当前页面时调用
   this.$refs.scroll.backTo(0,this.oldy);
   this.$refs.scroll.refresh();
},
deactivated:function(){
    //当离开当前页面时调用
    this.oldy=this.$refs.scroll.gety();

}
  
}
</script>

<style>
.home{
  height: 100vh;
}
.mint-header.is-fixed{
  /* position: sticky;
  top:0 */
  position: static;
}
.newlistbar{
  position: relative;
  z-index: 99;
}


</style>
