<template>
<div class="listbar">
 <span v-for="(value,index) in toptitle" :key="index"
 :class="{activestyle:nowindex==index}"  
 @click="beactive(index)">
 {{value}}
 </span>
 </div>
</template>

<script>
export default {
    data:function(){
      return{
         
         
        }
    },
   props:{
      nowindex:{
          type:Number,
          default:0
      },
        toptitle:{
            type:Array,
            default:()=>[]
        },
    },
    methods:{
      beactive:function(index){
         
          this.$emit("barclicked",index)

      },
    },
   
}
</script>

<style scoped>
.listbar{
    width: 100%;
    display: flex;
    flex: auto;
    justify-content: space-around;
    background-color: white;
}
.listbar span{
    padding:3px 0;
    margin: 5px 0;
    cursor: pointer;
}
.activestyle{
    border-bottom:3px solid rgb(247, 156, 171) ;
}
</style>