<template>
<div class="list">
<!-- <img src="./img/jq1.jpg" alt=""> -->
<!-- 要跳转路由的话不要用a标签，因为a标签
会有跳转行为，即使取消了也会导致第一次点击失效 -->
<a v-for="(value,index) in goods" 
:key="index"  @click="todetail(value.id)">
<!-- v-lazy:需要懒加载的图片 -->
<!-- <img v-lazy="value.imgsrc" alt="" @load="load"> -->
<img :src="value.imgsrc" alt="" @load="load">
<p class="goodsinfo">{{value.info}}</p>
<p class="price">{{value.price}}</p>
</a>
</div>      

</template>

<script>
export default {
    methods:{
      load:function(){
          this.$emit("imgload")
          //vue封装了一个load方法，
          //和js原生的onload作用一样
      },
      todetail:function(id){
          this.$router.push("/detail/"+id)
      }
    },
    props:{
     goods:{
             type:Array,
             default:()=>[]
         }
    }
}
/*
拼接数组的两种方式（遍历除外）：
1.a.push.apply(a,b);  拼接a、b，存入a中
2.c=a.concat(b)  拼接a、b，存入c中
*/ 
</script>

<style scoped>
.list{
  display:flex ;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}
.list a{
    display: block;
    width: 49%;
    box-shadow: 0 0 1px rgba(0,0,.1);
    margin: 3px 0;
    
}
.list img{
    width: 100%;
}
.goodsinfo{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.price{
    color:rgb(250, 173, 186);    
    text-align: center;
}

</style>