export function debounce(fun,delaytime=66){
    var timer;
    //这里使用闭包来延长变量生命周期,
    //就不使用有自己作用域的let来定义变量
    return function(...args){// ...表示可以传多个参数
         if(timer) clearTimeout(timer)
         timer=setTimeout(()=>{
           //fun.apply(this,args)
           //console.log("load")
           fun()
         },delaytime)
    }
  }





