import {req}  from  "./req"
export default function(){
        const that=this;   //this的指向可能会改变，以防万一
        //console.log(this)
        req({    //instance方法默认返回一个promise
     url:"/home/data",    //请求路径
    
      }).then((rel)=>{
           //对响应结果进行打印  
          //  console.log(rel);
           
           
          
           const tophalfdata=rel.topdata;
           that.topres=tophalfdata;
           
     const goods=rel.goodsdata;
     that.goodsres=goods;
     that.nowgoods=goods.pop;
    //  console.log(that.nowgoods)
      },(rej)=>{
       //响应失败  
       console.log(rej)
     })
    }
