<template>
<div class="recdbar">
<h3>推荐栏</h3>
<!-- <img src="./img/jq.png" alt=""> -->
<div class="itemc">
<a  :key="index"  v-for="(value,index) in recommend">
<img :src="value.imgsrc" alt="" @load="imgload">
<p >{{value.title}}</p>  
</a>

</div>

</div>
</template>

<script>

export default {
    data:function(){
        return{
          isload:false
        }
    },
    props:{
        recommend:{
            type:Array,
            //默认值为对象或数组要使用函数来返回，不然报错
            default:()=>[]
        }
    },
    methods:{
        imgload:function(){
           //由于该图片集只需要加载一张就能确定布局高度
           //出于性能考虑，我们处理为只发射一次事件
           if(!this.isload)
           {
                this.$emit("imgLoad");
                this.isload=true
           }
        }
    }
}
</script>

<style scoped>
.itemc{
    display: flex;
    flex: auto;
    justify-content: space-between;
    
}
.recdbar img{
    border-radius: 50%;
    width: 66px;
}
.recdbar p{
    text-align: center;
    margin-top: 6px;
}
.recdbar h3{
    text-align: center;
    margin: 10px 0;
    width: 100%;
}

</style>